/* General styles ***Revisar */

*
{
  --color-azul: #27a499;
  --color-gris: #9CAAA8;
  --color-verde: #055F51;
  --color-negro: #010505; /*#0b1a20*/
  --color-negro2: #0b1a20;
  --color-blanco: #F4F4F4;
  --color-azulClaro: #8AD9D1;
  --color-grisClaro: #585F5F;
}

.App 
{
  display: flex;
  text-align: center;
  justify-content: center;
  background-color: var(--color-azul);
}

.colorWhite
{
  color: var(--color-blanco);
}

.colorBlack
{
  color: var(--color-negro);
}

.backgroundBlack
{
  background-color: var(--color-negro);
}

.backgroundBlack2
{
  background-color: var(--color-negro2);
}

.backgroundGris
{
  background-color: var(--color-gris);
}

.backgroundWhite
{
  background-color: var(--color-blanco);
}

.backgroundAzul
{
  background-color: var(--color-azul);
}

.backgroundAzulClaro
{
  background-color: var(--color-azulClaro);
}

.backgroundVerde
{
  background-color: var(--color-verde);
}

/* Calculator styles */

.container-calculator
{
  width: 70%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: right;
}

.calculator
{
  height: 90%;
  width: 313px;
  display: flex;
  transition: 1s;
  text-align: center;
  align-items: center;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  box-shadow: 10px 10px 100px 0 var(--color-negro);
}

/* Styles the part top of calculator */

.container-button-changeMode-mic
{
  width: 100%;
  height: 20%;
  display: flex;
  align-items: start;
  border-radius: 20px;
  justify-content: space-between;
}

/* Styles of change mode */

.changeMode
{
  margin: 10px;
  display: flex;
  padding: 10px;
  transition: 1s;
  position: relative;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 20px 20px 0 20px;
}

.sun
{
  margin: 2px 15px 2px 0;
}

.moon
{
  margin: 2px;
}

.changeMode-button
{
  top: 9px;
  transition: 1s;
  width: 22px;
  height: 22px;
  position: absolute;
  border-radius: 10px;
}

  /* Style mic */

.mic
{
  width: 30px;
  border: none;
  height: 30px;
  transition: 1s;
  margin: 10px 0 0 10px;
  border-radius: 10px 10px 10px 0;
}

/* Screen styles */

.container-pantalla
{
  width: 90%;
  height: 20%;
  display: flex;
  align-items: end;
  overflow: hidden;
  flex-direction: column;
  justify-content: center;
}

  /* background-color: green; */

.operacion
{
  width: 90%;
  height: 30%;
  display: flex;
  align-items: end;
  font-size: large;
  padding-bottom: 7px;
  justify-content: right;
  color: var(--color-azul);
}

.resultado
{
  width: 90%;
  height: 30%;
  display: flex;
  align-items: end;
  font-size: xx-large;
  justify-content: right;
}

/* Styles of container buttons calculator */

.container-buttons-calculator
{
  width: 100%;
  height: 60%;
  display: grid;
  border-radius: 20px;
  grid-template-columns: repeat(4, 1fr);
}

.btn
{
  border: none;
  transition: 1s;
}

.equal
{
  grid-column: span 2;
}

.cornerDownLeft
{
  border-radius: 0 0 0 20px;
}

.cornerDownRight
{
  border-radius: 0 0 20px 0;
}

/* Styles of buttons of historial */

.container-buttons
{
  width: 30%;
  height: 100%;
  display: flex;
  padding-top: 30px;
  align-items: center;
  flex-direction: column;
}

.buttom-historial
{
  border: none;
  width: 150px;
  transition: 1s;
  font-size: small;
  margin-bottom: 20px;
  border-radius: 10px;
  padding: 4px 0 4px 0;
  box-shadow: 10px 10px 30px 0px var(--color-negro);
}

/* Styles when the brower is very small */

@media (max-width: 885px) {
  .App
  {
    align-items: center;
    flex-direction: column;
  }

  .container-calculator
  {
    justify-content: center;
  }

  .container-buttons
  {
    width: 70%;
    height: auto;
  }
}

/* Styles when clicked the view historial button */

.view-historial-clicked
{
  width: 70%;
  height: 300px;
  overflow-y: scroll;
  overflow-x: scroll;
}

.buttom-view li
{
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.buttom-view ul
{
  list-style: none;
  padding: 0;
}

.val-id
{
  margin-left: 40px;
  margin-right: 40px;
}

.val-operacion
{
  margin-right: 40px;
}